import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const galleryContainer = document.getElementById('gallery');
const loader = document.getElementById('loader'); // Ð£Ð±ÐµÐ´Ð¸ÑÑŒ, Ñ‡Ñ‚Ð¾ Ñƒ Ñ‚ÐµÐ±Ñ ÐµÑÑ‚ÑŒ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ñ id="loader"

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});

// ðŸ‘‰ 1. Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸
export function createGallery(images) {
  const markup = images
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => {
        return `
        <li class="gallery__item">
          <a href="${largeImageURL}" class="gallery__link">
            <img src="${webformatURL}" alt="${tags}" loading="lazy" class="gallery__image" />
          </a>
          <ul class="info">
            <li>
              <p>Likes</p>
              <span>${likes}</span>
            </li>
            <li>
              <p>Views</p>
              <span>${views}</span>
            </li>
            <li>
              <p>Comments</p>
              <span>${comments}</span>
            </li>
            <li>
              <p>Downloads</p>
              <span>${downloads}</span>
            </li>
          </ul>
        </li>
      `;
      }
    )
    .join('');

  galleryContainer.insertAdjacentHTML('beforeend', markup);
  lightbox.refresh();
}

// ðŸ‘‰ 2. ÐžÑ‡Ð¸ÑÑ‚ÐºÐ° Ð³Ð°Ð»ÐµÑ€ÐµÐ¸
export function clearGallery() {
  galleryContainer.innerHTML = '';
}

// ðŸ‘‰ 3. ÐŸÐ¾ÐºÐ°Ð·Ð°Ñ‚ÑŒ Ð»Ð¾Ð°Ð´ÐµÑ€
export function showLoader() {
  loader.classList.remove('hidden'); // ÐšÐ»Ð°ÑÑ hidden Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐºÑ€Ñ‹Ð²Ð°Ñ‚ÑŒ Ð»Ð¾Ð°Ð´ÐµÑ€ Ð² CSS
}

// ðŸ‘‰ 4. Ð¡Ð¿Ñ€ÑÑ‚Ð°Ñ‚ÑŒ Ð»Ð¾Ð°Ð´ÐµÑ€
export function hideLoader() {
  loader.classList.add('hidden');
}
